.mode-chart-container .title {
  height: 24px; }
  .mode-chart-container .title .name {
    color: #343434;
    font-size: 16px;
    font-weight: 400;
    display: inline-block;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    max-width: 300px; }
  .mode-chart-container .title .tag {
    float: right;
    width: 64px;
    height: 24px;
    line-height: 24px;
    font-size: 14px;
    font-weight: 600;
    color: #fff;
    background: #40b3ff;
    text-align: center;
    border-radius: 12px;
    vertical-align: 4px; }

.mode-chart-container .options {
  display: inline-block; }
  .mode-chart-container .options .ant-select .ant-select-selection {
    background: transparent;
    border: none;
    box-shadow: none; }
  .mode-chart-container .options .ant-select .ant-select-selection__rendered {
    margin-right: 30px;
    margin-left: 0; }

.mode-chart-container .unit {
  float: right;
  color: #343434;
  margin-top: 5px;
  font-size: 14px; }

.mode-chart-container .active canvas {
  cursor: pointer !important; }
