.select__member {
  width: 660px !important; }
  .select__member .ant-modal-title {
    margin: 0 auto;
    font-weight: bold; }
  .select__member .ant-modal-content {
    width: 661px; }
  .select__member .ant-modal-body {
    padding: 0; }
    .select__member .ant-modal-body::after {
      display: block;
      content: '';
      clear: both; }
  .select__member .modal__lf, .select__member .modal__lr {
    float: left;
    overflow: hidden;
    height: 516px;
    padding: 20px 0;
    box-sizing: border-box; }
  .select__member .modal__lf {
    width: 280px;
    border-right: solid 1px var(--border-gray); }
  .select__member .no-data {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    color: #d3d3d3; }
  .select__member .modal__lr {
    width: 380px; }
    .select__member .modal__lr ul.scroll__list {
      list-style: none;
      height: calc(100% - 84px); }
      .select__member .modal__lr ul.scroll__list .delete {
        float: right;
        cursor: pointer;
        width: 14px;
        height: 14px;
        line-height: initial;
        margin-top: 4px; }
      .select__member .modal__lr ul.scroll__list li {
        padding: 5px 0; }
  .select__member .ant-input-search-icon {
    display: none; }
  .select__member .modal__search__icon {
    background: #eee; }
  .select__member .scroll__list {
    width: 100%;
    height: calc(100% - 70px);
    overflow-y: auto;
    overflow-x: hidden;
    padding: 0 20px; }
    .select__member .scroll__list::-webkit-scrollbar {
      width: 3px; }
    .select__member .scroll__list img {
      vertical-align: -7px;
      width: 24px;
      height: 24px;
      margin-right: 10px; }
  .select__member .ant-input-search input {
    background: #F5F5F5;
    border-radius: 18px;
    border: none;
    padding: 4px 11px 4px 32px !important;
    box-sizing: border-box; }
    .select__member .ant-input-search input:focus {
      border: solid 1px var(--theme-primary) !important; }
  .select__member .ant-input-search .ant-input-suffix {
    left: 12px;
    right: unset; }
  .select__member .btns {
    text-align: right;
    padding: 0 20px; }
    .select__member .btns button:first-child {
      margin-right: 10px; }
  .select__member .list__head {
    padding: 0 20px; }
    .select__member .list__head .tips {
      font-size: 12px;
      margin: 10px 0 5px; }
  .select__member .modal__list {
    width: 100%; }
    .select__member .modal__list .ant-checkbox-wrapper {
      display: table;
      width: 100%;
      margin: 10px 0; }
      .select__member .modal__list .ant-checkbox-wrapper .ant-checkbox {
        float: right;
        margin-top: 4px; }
      .select__member .modal__list .ant-checkbox-wrapper img {
        float: left; }
      .select__member .modal__list .ant-checkbox-wrapper .content {
        width: 170px;
        float: left; }
        .select__member .modal__list .ant-checkbox-wrapper .content > span {
          display: block; }
          .select__member .modal__list .ant-checkbox-wrapper .content > span.department {
            color: #909090; }
      .select__member .modal__list .ant-checkbox-wrapper > span:last-child {
        float: left;
        width: 100%;
        width: calc(100% - 20px);
        padding-left: 0;
        font-weight: 500;
        font-size: 14px; }
