.merge__post {
  width: 348px;
  word-break: break-all;
  cursor: pointer; }
  .merge__post * {
    cursor: pointer; }
  .merge__post h4 {
    font-size: 14px;
    font-weight: 600; }
  .merge__post .view-more {
    margin-top: 8px;
    color: #B0B0B0;
    font-size: 12px; }
  .merge__post .merge__post__item {
    margin: 5px 0;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    /*! autoprefixer: off */
    -webkit-box-orient: vertical; }
    .merge__post .merge__post__item label {
      margin-bottom: 0; }
      .merge__post .merge__post__item label::after {
        display: inline-block;
        content: ':';
        margin-right: 10px; }

.merge__post__modal .merge__post {
  width: 100%; }
  .merge__post__modal .merge__post .post-image__column, .merge__post__modal .merge__post .meeting-class {
    margin-top: 5px;
    border: 1px solid #dcdce0;
    border-radius: 12px; }

.merge__post__modal .merge__card {
  margin-top: 5px;
  width: 348px;
  border: 1px solid #dcdce0;
  border-radius: 12px;
  box-sizing: border-box;
  padding: 10px 15px; }

.merge__post__modal .ant-modal-body {
  max-height: 60vh;
  overflow-y: auto; }

.merge__post__modal .merge__item {
  color: #616161;
  margin-bottom: 20px; }
  .merge__post__modal .merge__item .merge__info {
    margin-bottom: 2px; }
    .merge__post__modal .merge__item .merge__info span {
      font-size: 14px;
      margin-right: 10px; }
    .merge__post__modal .merge__item .merge__info time {
      font-size: 12px;
      color: #909090;
      font-weight: 400; }
  .merge__post__modal .merge__item .file__content {
    min-height: 200px !important; }
