.collect-item:hover {
  background: #F5F5F5; }

.collect-item .collect-item-inner {
  position: relative;
  margin-left: 20px;
  padding: 20px 30px 20px 0;
  border-bottom: 1px solid #F0F0F0; }

.collect-item .header {
  display: flex;
  align-items: center; }
  .collect-item .header .name {
    max-width: 190px;
    color: #1D1C1D;
    font-size: 14px;
    font-weight: 600;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap; }
  .collect-item .header .time {
    margin: 0 8px;
    color: #909090;
    font-size: 12px; }
  .collect-item .header .flag-icon__container, .collect-item .header .merge-collect-icon {
    height: 15px;
    font-size: 0; }
    .collect-item .header .flag-icon__container svg, .collect-item .header .merge-collect-icon svg {
      width: 12px; }

.collect-item .content {
  margin-top: 4px;
  color: #616161;
  font-size: 14px; }
  .collect-item .content .card-wrap {
    margin-top: 10px;
    background: #fff; }
  .collect-item .content .merge__post {
    width: 100%; }

.collect-item .card-type-5 {
  border: 1px solid #e6e6e6; }

.collect-item .card-type-11-1 {
  padding: 10px;
  border: 1px solid #e6e6e6; }

.collect-item .message-text.text-overflow {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden; }

.collect-item .message-text p {
  margin: 0;
  word-break: break-all; }

.collect-item .view-more {
  margin-top: 8px;
  color: #B0B0B0;
  font-size: 12px;
  cursor: pointer; }
  .collect-item .view-more .anticon-right svg {
    width: 10px; }

.collect-item .post-image__column {
  margin: 0; }

.collect-item .more-icon {
  position: absolute;
  top: 12px;
  right: 10px;
  display: inline-block;
  width: 30px;
  height: 40px;
  background: url(/static/files/7b3a1e256a06068e8cf576906526b6f9.png) no-repeat left center;
  background-size: 30px; }

.my-collect-dropdown {
  color: #343434; }
  .my-collect-dropdown i {
    display: inline-block;
    width: 13px;
    height: 13px;
    background-repeat: no-repeat;
    background-size: contain;
    vertical-align: -1px; }
  .my-collect-dropdown .repost-icon {
    background-image: url(/static/files/c24975ab6430f9b323c6dd3d22e9e102.png); }
  .my-collect-dropdown .delete-icon {
    background-image: url(/static/files/a137c47fb2e60437f6ce96ecf21ac7b6.png); }
