.settings-modal {
  width: unset;
  border-radius: 12px;
  overflow: hidden; }
  .settings-modal.general .ant-modal-body {
    padding: 62px 0;
    min-height: auto !important; }
    .settings-modal.general .ant-modal-body .ant-card {
      margin: 0 auto; }
  .settings-modal.notifications .ant-modal-body, .settings-modal.display .ant-modal-body {
    height: 456px; }
  .settings-modal .modal-content {
    height: 100%; }
  .settings-modal label {
    font-weight: normal !important; }
  .settings-modal .modal-header {
    text-align: center;
    height: 44px;
    min-height: 44px;
    background: #f8f8fb !important;
    padding: 0; }
    .settings-modal .modal-header .modal-title {
      width: 100%;
      height: 100%;
      max-width: 100%;
      line-height: 44px;
      color: #343434 !important; }
    .settings-modal .modal-header .close {
      margin-top: 12px;
      background: #C8CBD2;
      border-radius: 50%;
      left: 18px;
      width: 16px;
      height: 16px;
      line-height: 16px;
      font-size: 14px;
      text-align: center; }
  .settings-modal .ant-modal-body {
    background: #fff;
    min-height: 50vh !important;
    max-height: 80vh;
    overflow-y: auto; }
  .settings-modal.general {
    width: 660px !important; }
    .settings-modal.general .ant-modal-body {
      max-height: 70vh;
      min-height: unset !important;
      padding: 62px 130px !important;
      background: url(/static/files/778cfe3d79d1d988c532188adb7b9751.svg);
      background-size: 100%;
      background-position: top left; }
      .settings-modal.general .ant-modal-body .settings-content {
        padding: 0; }
  .settings-modal .ant-radio-wrapper,
  .settings-modal .ant-checkbox-wrapper {
    display: block;
    margin: 8px 0; }
  .settings-modal .radio__item {
    font-size: 14px;
    margin-top: 20px; }
    .settings-modal .radio__item h3 {
      font-size: 14px;
      font-weight: bold; }
