.channel-header-content .top-icon {
  display: inline-flex;
  align-items: center;
  color: #909090;
  cursor: pointer;
  vertical-align: middle; }
  .channel-header-content .top-icon:before {
    content: '';
    display: inline-block;
    margin-right: 3px;
    width: 14px;
    height: 15px;
    background: url(/static/files/35d96f12d119723e4ae2cf2a68c60a1b.png) no-repeat;
    background-size: 100%; }
  .channel-header-content .top-icon:hover:before {
