.meet_item {
  height: 100px;
  padding: 0 15px;
  transition: all .12s; }
  .meet_item:hover {
    background: rgba(0, 188, 112, 0.1); }
  .meet_item .meet_item-inner {
    position: relative;
    box-sizing: border-box;
    height: 100%; }
  .meet_item .meet_time {
    color: #909090;
    font-size: 12px; }
  .meet_item.to_in .meet_item-inner {
    border-radius: 4px;
    padding: 0 10px; }
  .meet_item.to_in .meet_header {
    padding-top: 24px;
    font-size: 14px;
    font-weight: 700;
    color: #000;
    margin-right: 51px; }
  .meet_item.to_in .meet_time {
    margin-top: 20px; }
  .meet_item.un_in .meet_item-inner {
    border-bottom: 1px solid #F0F0F0; }
  .meet_item.un_in .meet_header {
    padding-top: 24px;
    font-size: 14px;
    color: #343434;
    line-height: 20px;
    margin-right: 51px; }
  .meet_item.un_in .meet_time {
    margin-top: 6px; }
  .meet_item button.join-btn {
    position: absolute;
    right: 10px;
    bottom: 10px; }
  .meet_item button.join-btn {
    font-size: 14px;
    color: #fff;
    background: #00BC70;
    border-radius: 4px;
    border-color: #00BC70; }
    .meet_item button.join-btn:hover, .meet_item button.join-btn.active {
      color: #fff;
      background: #00BC70; }
  .meet_item .status {
    width: 51px;
    height: 18px;
    position: absolute;
    right: 0;
    top: 25px;
    line-height: 18px;
    text-align: center;
    font-size: 12px;
    font-weight: 600; }
    .meet_item .status .border {
      position: absolute;
      width: 100%;
      height: 100%;
      transform: skewX(-20deg);
      border-radius: 4px;
      border: 1px solid transparent; }
  .meet_item .status_unin {
    color: #FFA83E; }
    .meet_item .status_unin .border {
      border-color: #FFA83E; }
  .meet_item .status_ended {
    color: #C0C0C0; }
    .meet_item .status_ended .border {
      border-color: #C0C0C0; }
  .meet_item .status_ing {
    color: #FF0000; }
    .meet_item .status_ing .border {
      border-color: #FF0000; }
