.post-right-comments-container {
  padding-bottom: 30px; }

.post__right__scrollbars > .scrollbar--view {
  overflow-x: hidden !important; }

.post__right__scrollbars .post-right__scroll {
  padding-top: 0; }

.right__sidebar__comment-item {
  width: 100%;
  padding: 0 20px; }
  .right__sidebar__comment-item:hover {
    background: #F5F5F5; }
  .right__sidebar__comment-item.root__post {
    background: #E5F8F0; }
    .right__sidebar__comment-item.root__post:hover {
      background: #F5F5F5; }
  .right__sidebar__comment-item > div {
    border-bottom: solid 1px #f0f0f0;
    display: table;
    width: 100%;
    padding: 20px 0; }
  .right__sidebar__comment-item .post-img {
    width: 36px;
    float: left;
    cursor: pointer; }
  .right__sidebar__comment-item .post-content {
    float: left;
    width: calc(100% - 104px) !important;
    padding-left: 12px;
    position: relative; }
    .right__sidebar__comment-item .post-content .dot__new__menu {
      position: absolute;
      top: 0;
      right: -68px; }
    .right__sidebar__comment-item .post-content .post-image__columns {
      min-width: 236px; }
    .right__sidebar__comment-item .post-content .post__info {
      height: 24px;
      line-height: 24px; }
      .right__sidebar__comment-item .post-content .post__info time {
        font-size: 12px;
        color: #909090;
        margin: 0 5px; }
  .right__sidebar__comment-item .post__msg span {
    word-break: break-all; }
  .right__sidebar__comment-item .post__msg .post-message__text {
    border: none;
    padding: 0;
    background: transparent; }
    .right__sidebar__comment-item .post__msg .post-message__text p {
      word-break: break-all; }
    .right__sidebar__comment-item .post__msg .post-message__text .emoticon {
      width: 21px;
      min-width: 21px;
      height: 21px;
      min-height: 21px; }
  .right__sidebar__comment-item.current__user .post-img {
    float: right; }
  .right__sidebar__comment-item.current__user .post-content {
    float: left;
    width: calc(100% - 36px) !important; }
    .right__sidebar__comment-item.current__user .post-content .post__info > div, .right__sidebar__comment-item.current__user .post-content .post__info > time {
      float: right; }
    .right__sidebar__comment-item.current__user .post-content .post__msg span {
      float: right; }
  .right__sidebar__comment-item.current__user .dot__new__menu {
    right: unset; }
