.new-invite-modal .inner {
  display: flex;
  height: 426px; }

.new-invite-modal .left {
  width: 280px;
  overflow: auto;
  border-right: 1px solid #F0F0F0; }
  .new-invite-modal .left .checkbox-hide > .ant-tree-checkbox {
    display: none; }
  .new-invite-modal .left .user-node {
    padding-left: 11px; }
    .new-invite-modal .left .user-node > .ant-tree-switcher {
      display: none; }
  .new-invite-modal .left .input-box {
    position: relative;
    margin-bottom: 20px; }
    .new-invite-modal .left .input-box:hover .search__icon svg {
      stroke: #00BC70; }
    .new-invite-modal .left .input-box.focused input {
      border: 1px solid #00BC70; }
    .new-invite-modal .left .input-box.focused .search__icon svg {
      stroke: #00BC70; }
  .new-invite-modal .left .loading-cont {
    margin-top: 50px;
    text-align: center; }
  .new-invite-modal .left .search-members {
    padding: 0 20px 0 40px;
    width: 250px;
    height: 36px;
    line-height: 36px;
    border-radius: 18px;
    background: #F5F5F5;
    border: 0; }
  .new-invite-modal .left .search-list {
    padding: 0 23px 0 7px;
    list-style: none; }
    .new-invite-modal .left .search-list li {
      position: relative;
      margin-top: 18px;
      padding-left: 32px; }
      .new-invite-modal .left .search-list li img {
        position: absolute;
        left: 0;
        top: 2px;
        width: 24px; }
    .new-invite-modal .left .search-list .title {
      color: #343434;
      font-size: 14px;
      font-weight: bold; }
    .new-invite-modal .left .search-list .department {
      color: #909090;
      font-size: 12px; }
    .new-invite-modal .left .search-list .ant-checkbox-wrapper {
      position: absolute;
      right: 0;
      top: 0; }
  .new-invite-modal .left .search-no-data {
    margin-top: 170px;
    padding-right: 23px;
    text-align: center; }

.new-invite-modal .right {
  flex: 1;
  padding: 5px 10px 0 20px;
  overflow: hidden;
  overflow-y: auto; }
  .new-invite-modal .right .title {
    color: #909090; }
  .new-invite-modal .right .list > div {
    margin-top: 20px;
    display: flex;
    align-items: center; }
    .new-invite-modal .right .list > div img {
      margin-right: 8px;
      width: 24px; }
    .new-invite-modal .right .list > div .name {
      flex: 1;
      color: #343434; }
    .new-invite-modal .right .list > div .anticon-close-circle {
      width: 14px;
      color: #C8CBD2; }
