.scaffold-dependency {
  width: 450px;
  position: relative;
  padding-bottom: 35px; }
  .scaffold-dependency label {
    font-weight: 600; }
  .scaffold-dependency .ant-form-item-required::before {
    display: none; }

.scaffold-dependency-title {
  margin-top: 8px;
  margin-bottom: 30px;
  font-size: 14px; }

.scaffold-dependency-actions {
  position: absolute;
  bottom: -10px;
  left: -15px;
  right: -15px;
  border-top: 1px solid #E6E6E6; }

.scaffold-dependency-action {
  color: #00BC70 !important;
  line-height: 40px;
  text-align: center;
  display: inline-block;
  width: 100%; }
