.scaffold-meta {
  width: 450px;
  position: relative;
  padding-bottom: 35px; }
  .scaffold-meta label {
    font-weight: 600; }
  .scaffold-meta .ant-form-item-required::before {
    display: none; }

.scaffold-meta-disabled {
  padding-bottom: 0; }
  .scaffold-meta-disabled .scaffold-meta-actions {
    display: none; }

.scaffold-meta-title {
  margin-top: 8px;
  margin-bottom: 22px;
  font-size: 14px; }
  .scaffold-meta-title .ant-input:focus {
    outline: none !important;
    outline-offset: 0 !important;
    box-shadow: none;
    border: 0;
    border-bottom: 1px solid #F0F0F0; }

.scaffold-meta-actions {
  position: absolute;
  bottom: -10px;
  left: -15px;
  right: -15px;
  border-top: 1px solid #E6E6E6; }
  .scaffold-meta-actions::after {
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 20px;
    width: 2px;
    background: #E6E6E6; }

.scaffold-meta-action {
  color: #00BC70 !important;
  line-height: 40px;
  text-align: center;
  display: inline-block;
  width: 50%; }
