.module-guide-modal .item {
  text-align: center; }

.module-guide-modal p:nth-child(1) {
  margin: 24px 0 9px;
  color: #343434;
  font-size: 24px;
  font-weight: 600; }

.module-guide-modal p:nth-child(2) {
  margin: 0;
  color: #616161;
  font-size: 14px; }

.module-guide-modal img {
  margin: 20px auto;
  width: 492px; }

.module-guide-modal .ant-carousel .slick-dots li button {
  margin: 0 5px;
  width: 7px;
  height: 7px;
  border-radius: 100%;
  background: #E0E0E0; }

.module-guide-modal .ant-carousel .slick-dots li.slick-active button {
  background: #00BC70; }

.module-guide-modal .prev-btn, .module-guide-modal .next-btn {
  position: absolute;
  top: 270px;
  display: block;
  width: 14px;
  height: 24px;
  color: #B0B0B0;
  font-size: 24px;
  background-repeat: no-repeat;
  background-size: 100%; }

.module-guide-modal .prev-btn {
  left: 27px;
  background-image: url(/static/files/ad985e802ada3ed816a15768633a28df.png); }
  .module-guide-modal .prev-btn:hover {
    background-image: url(/static/files/822e679b0b7d580994302c05791c1686.png); }

.module-guide-modal .next-btn {
  right: 27px;
  background-image: url(/static/files/40cb177fffab149216e47956794ca884.png); }
  .module-guide-modal .next-btn:hover {
    background-image: url(/static/files/64cb6474af292233efcb5cd57aca6cc0.png); }
