#search-result {
  position: absolute;
  top: 70px;
  width: 390px;
  height: 450px;
  background: white;
  box-shadow: 0px 2px 21px 0px #dcdce0;
  border-radius: 4px; }
  #search-result .ant-tabs {
    height: 100%; }
  #search-result .ant-tabs-tab {
    padding: 18px 8px 10px 8px;
    margin: 0; }
  #search-result .ant-tabs-tab + .ant-tabs-tab {
    margin-left: 17px;
    margin-right: 0; }
  #search-result .ant-tabs-ink-bar {
    background-color: #00BC70; }
  #search-result .ant-tabs-nav {
    padding: 0 30px; }
    #search-result .ant-tabs-nav .ant-tabs-tab:hover,
    #search-result .ant-tabs-nav .ant-tabs-tab-active {
      color: #00BC70; }
  #search-result .ant-tabs-bar {
    margin: 0; }
  #search-result .ant-tabs-content {
    height: 402px; }
  #search-result .ant-tabs-tabpane {
    padding: 0 0 0 0;
    overflow-y: auto; }
  #search-result .result-section + .result-section {
    margin-top: 15px; }
  #search-result .result-section-title {
    color: #909090;
    font-size: 14px;
    line-height: 20px;
    border-bottom: 1px solid #F0F0F0;
    padding: 26px 0 6px 0;
    margin: 0 20px 0 20px;
    position: relative; }
  #search-result .result-section-more {
    position: absolute;
    right: 0;
    bottom: 6px;
    font-size: 12px;
    color: #00BC70;
    cursor: pointer; }
