.shipconnect {
  display: flex;
  flex-direction: column;
  width: 480px;
  color: #343434; }
  .shipconnect > .top {
    display: flex;
    justify-content: space-between;
    height: 24px;
    font-size: 16px;
    font-family: PingFangSC-Regular; }
    .shipconnect > .top > .left {
      font-weight: 400; }
    .shipconnect > .top > .right {
      width: 64px;
      height: 24px;
      font-weight: 400;
      border-radius: 12px;
      font-size: 14px;
      font-family: PingFangSC-Semibold;
      font-weight: 600;
      color: #ffffff;
      display: flex;
      align-items: center;
      justify-content: center; }
      .shipconnect > .top > .right.success {
        background: #00BC70; }
  .shipconnect > .bottom {
    margin-top: 10px;
    min-height: 30px;
    font-size: 12px;
    font-weight: 500; }
    .shipconnect > .bottom > table {
      width: 100%; }
      .shipconnect > .bottom > table > thead > tr {
        line-height: 32px;
        background: #F2F2F2; }
        .shipconnect > .bottom > table > thead > tr > th:nth-child(1) {
          width: 76px;
          text-align: center; }
        .shipconnect > .bottom > table > thead > tr > th:nth-child(2) {
          width: 137px; }
        .shipconnect > .bottom > table > thead > tr > th:nth-child(3) {
          width: 137px; }
      .shipconnect > .bottom > table > tbody {
        color: #616161;
        font-size: 12px; }
        .shipconnect > .bottom > table > tbody a {
          color: #00BC70 !important; }
        .shipconnect > .bottom > table > tbody > tr {
          line-height: 24px; }
          .shipconnect > .bottom > table > tbody > tr:nth-child(even) {
            background: #FAFAFA; }
          .shipconnect > .bottom > table > tbody > tr > td:nth-child(1) {
            width: 72px;
            text-align: center; }
