.trace {
  width: 380px;
  color: #343434;
  cursor: pointer; }
  .trace > .top {
    display: flex;
    justify-content: space-between;
    height: 24px;
    font-size: 16px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    margin-bottom: 10px; }
    .trace > .top > .right {
      width: 64px;
      height: 24px;
      border-radius: 12px;
      font-size: 14px;
      font-family: PingFangSC-Semibold;
      font-weight: 600;
      color: #ffffff;
      display: flex;
      align-items: center;
      justify-content: center; }
      .trace > .top > .right.warn {
        background: #40B3FF;
        color: #fff; }
  .trace > .middle .name {
    color: #343434;
    font-size: 16px;
    margin-bottom: 12px; }
  .trace > .middle .cost {
    color: #00BCD5;
    font-size: 16px;
    background: url(/static/files/fc945e3736c565bd965f4c94723f59b6.png) no-repeat left center;
    background-size: 16px 16px;
    padding-left: 20px;
    line-height: 22px; }
  .trace > .bottom {
    font-size: 13px;
    margin-top: 10px;
    min-height: 30px;
    position: relative; }
    .trace > .bottom .span {
      border-radius: 11px;
      background: url(/static/files/5078385020ab1d5b0caf5d7b83b60d19.png) no-repeat 10px center;
      background-size: 10px 8px;
      background-color: #f2f2f2;
      padding: 0 10px 0 25px;
      line-height: 22px;
      display: inline-block; }
    .trace > .bottom .time {
      color: #909090;
      position: absolute;
      top: 0;
      right: 0; }
