@charset "UTF-8";
.contactsItem {
  color: #909090;
  padding: 0 20px 0 20px;
  cursor: pointer; }
  .contactsItem:hover {
    background: rgba(0, 188, 112, 0.1); }
  .contactsItem .contactsItem-inner {
    display: flex;
    align-items: center;
    position: relative;
    -moz-user-select: none;
    /*火狐*/
    -webkit-user-select: none;
    /*webkit浏览器*/
    -ms-user-select: none;
    /*IE10*/
    -khtml-user-select: none;
    /*早期浏览器*/
    user-select: none; }
  .contactsItem img {
    width: 36px;
    height: 36px;
    border-radius: 50%; }
  .contactsItem .left {
    display: inline-block;
    margin-left: 10px; }
  .contactsItem .user-name {
    font-size: 16px;
    color: #343434;
    height: 22px;
    width: 78px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap; }
  .contactsItem .right {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 12px;
    max-width: 172px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap; }
