.sidebar--right__header .header-right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 1; }

.sidebar--right__header .search-cont {
  margin-left: 10px;
  flex: 1; }
  .sidebar--right__header .search-cont .search-btn {
    position: relative;
    top: 2px; }
    .sidebar--right__header .search-cont .search-btn svg {
      stroke: #B0B0B0;
      transform: scale(1.3); }
  .sidebar--right__header .search-cont .input-box {
    position: relative;
    line-height: initial; }
    .sidebar--right__header .search-cont .input-box input {
      padding: 0 20px 0 40px;
      width: 100%;
      height: 36px;
      line-height: 36px;
      border-radius: 18px;
      background: #F5F5F5;
      border: 0; }
    .sidebar--right__header .search-cont .input-box:hover .search__icon svg {
      stroke: #00BC70; }
    .sidebar--right__header .search-cont .input-box.focused input {
      border: 1px solid #00BC70; }
    .sidebar--right__header .search-cont .input-box.focused .search__icon svg {
      stroke: #00BC70; }
