.cloud-wrap .cloud-header {
  display: flex;
  padding-top: 10px;
  border-bottom: 1px solid #e8e8e8; }

.cloud-wrap .ant-tabs {
  flex: 1; }

.cloud-wrap .ant-tabs-tab {
  margin: 0 17px;
  padding: 0 0 10px; }

.cloud-wrap .ant-tabs-ink-bar {
  height: 3px;
  background-color: #00BC70; }

.cloud-wrap .ant-tabs-nav .ant-tabs-tab:hover,
.cloud-wrap .ant-tabs-nav .ant-tabs-tab-active {
  color: #00BC70; }

.cloud-wrap .ant-tabs-bar {
  margin: 0;
  border-bottom: none; }

.cloud-wrap .cloud-cont {
  margin-left: 20px; }

.cloud-wrap .sort-icon {
  display: inline-block;
  margin: 3px 15px 0 0;
  width: 18px;
  height: 18px;
  background: url(/static/files/ebe685eb320ba7f17c039c236da29bfb.png) no-repeat;
  background-size: contain; }
  .cloud-wrap .sort-icon:hover {
    background-image: url(/static/files/4572c7b731621c98b8e23e4ce9927647.png); }

.cloud-wrap .no-data, .cloud-wrap .loading-cont {
  margin-top: 30px;
  text-align: center; }
