.post-dropdown {
  position: relative;
  z-index: 10; }
  .post-dropdown .more-icon {
    display: inline-block;
    width: 30px;
    height: 40px;
    background: url(/static/files/7b3a1e256a06068e8cf576906526b6f9.png) no-repeat left center;
    background-size: 30px; }

.post-dropdown-inner {
  color: #343434; }
  .post-dropdown-inner .forward svg {
    vertical-align: middle; }
  .post-dropdown-inner .flag svg {
    width: 13px;
    height: 13px; }
  .post-dropdown-inner .download a, .post-dropdown-inner .download a:hover {
    color: #343434; }
