.cloud-item {
  display: flex;
  align-items: center;
  padding: 20px 0;
  border-bottom: 1px solid #F0F0F0; }
  .cloud-item .cover {
    width: 42px; }
  .cloud-item .intro {
    margin: 0 8px;
    flex: 1; }
    .cloud-item .intro .title {
      margin: 0 0 5px;
      color: #1F2D3D;
      font-weight: bold; }
    .cloud-item .intro .details {
      color: #909090;
      font-size: 12px; }
      .cloud-item .intro .details span {
        padding: 0 6px;
        border-left: 1px solid #F0F0F0; }
        .cloud-item .intro .details span:first-child {
          padding-left: 0;
          border-left: none; }
  .cloud-item .post-dropdown {
    margin-right: 8px; }
