.ao-action-container {
  position: relative;
  padding-bottom: 40px; }

.ao-action-container-actions {
  position: absolute;
  bottom: -10px;
  left: -15px;
  right: -15px;
  border-top: 1px solid #E6E6E6;
  display: flex; }

.ao-action-container-action {
  color: #00BC70 !important;
  line-height: 40px;
  text-align: center;
  flex-grow: 1;
  cursor: pointer; }

.ao-action-container-action + .ao-action-container-action {
  position: relative; }
  .ao-action-container-action + .ao-action-container-action::after {
    content: ' ';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-50%, -50%);
    height: 20px;
    width: 2px;
    background: #E6E6E6; }
