  height: 30px;
  box-sizing: border-box;
  overflow: hidden;
  background: #fff; }
  .dot__new__menu > span {
    display: inline-block;
    width: 50%;
    height: 100%;
    text-align: center;
    cursor: pointer;
    box-sizing: border-box; }
    .dot__new__menu > span .flag-icon__container {
      visibility: visible;
      height: 100%;
      width: 100%;
      margin: 0;
      top: 0; }
      .dot__new__menu > span .flag-icon__container svg {
        margin: 4px 0 0 2px; }
    .dot__new__menu > span:first-child {
