.ao-userprofile-header {
  border-bottom: 1px solid #F0F0F0;
  padding: 0 50px 20px 0;
  margin: 0 0 27px 0;
  position: relative; }

.ao-userprofile-avatar {
  border-radius: 50%;
  width: 48px;
  height: 48px;
  position: absolute;
  top: 0;
  right: 0; }

.ao-userprofile-name {
  font-size: 22px;
  color: #343434;
  font-weight: 600;
  line-height: 30px; }

.ao-userprofile-gender {
  display: inline-block;
  margin-left: 10px;
  width: 16px; }

.ao-userprofile-org {
  color: #616161;
  margin-top: 2px; }

.ao-userprofile-actions {
  margin-top: 28px;
  text-align: right; }
