.meeting-invitation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100; }

.meeting-invitation-mask {
  width: 100%;
  height: 100%; }

.meeting-invitation-main {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #ffffff;
  width: 380px;
  box-shadow: 0px 6px 12px 0px rgba(0, 33, 69, 0.16), 0px 4px 8px 0px rgba(0, 33, 69, 0.08);
  border-radius: 4px 4px 4px 4px; }

.meeting-invitation-content {
  padding: 0 0 0 80px;
  position: relative;
  padding: 15px 20px 15px 90px; }

.meeting-invitation-avatar {
  position: absolute;
  top: 15px;
  left: 20px;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: #dcd6d6; }

.meeting-invitation-title {
  color: #343434;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 5px; }

.meeting-invitation-countdown {
  color: #909090;
  font-size: 12px;
  line-height: 17px; }

.meeting-invitation-actions {
  display: flex;
  flex-direction: row;
  border-top: 1px solid #EFF3F7; }
  .meeting-invitation-actions img {
    height: 20px;
    width: 20px;
    margin-right: 12px; }
  .meeting-invitation-actions > * {
    text-align: center;
    padding: 15px 0;
    flex-grow: 1;
    cursor: pointer; }
    .meeting-invitation-actions > *:first-child {
      color: #343434; }
    .meeting-invitation-actions > *:last-child {
      color: #00BC70; }
