.ao-channel-member {
  border-bottom: 1px solid #F0F0F0;
  margin: 0 -1em;
  position: relative; }

.ao-channel-member-actions {
  position: absolute;
  right: 30px;
  top: 0;
  background: #FFFFFF;
  box-shadow: 0px 1px 5px 0px #e9e9e9;
  border-radius: 8px;
  z-index: 1; }
  .ao-channel-member-actions > a {
    padding: 5px 15px;
    font-size: 14px;
    display: block;
    color: #343434 !important; }
    .ao-channel-member-actions > a:hover {
      background: rgba(0, 188, 112, 0.1); }

.ao-channel-member-menus {
  position: absolute;
  top: 36%;
  right: 10px;
  width: 30px;
  height: 40px;
  background: url(/static/files/7b3a1e256a06068e8cf576906526b6f9.png) no-repeat left center;
  background-size: 30px; }

.ao-channel-member-point {
  width: 3px;
  height: 3px;
  background: #00BC70;
  border-radius: 50%; }

.ao-channel-member-point + .ao-channel-member-point {
  margin-top: 1px; }
